@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;600;700&display=swap);
.swal-text {
  font-family: Poppins;
}
.swal-title {
  font-family: Poppins;
}
.swal-button {
  font-family: Poppins;
  background-color: #3ab2b3;
}

.react-tabs__tab-list {
  border-bottom: none;
  margin: 0 0 85px;
}

.react-tabs__tab {
  border: none;
  font-family: Poppins;
  list-style: none;
  padding: 6px 25px;
  cursor: pointer;
  font-weight: 600;
  border-bottom: 2px solid #ccc;
  display: inline-block;
}

.react-tabs__tab--selected {
  background: #fff;
  color: #3ab2b3;
  border: none;
  border-bottom: 2px solid #3ab2b3;
}

@media screen and (min-width: 320px) and (max-width: 525px) {
  .react-tabs__tab {
    font-size: 12px;
    padding: 3px 16px;
  }
  .react-tabs__tab:last-child {
    margin-top: 10px;
  }
  .react-tabs {
    padding: 0 10px;
  }
}

@media (min-width: 526px) and (max-width: 768px) {
  .react-tabs__tab {
    font-size: 15px;
    padding: 3px 16px;
  }
  .react-tabs__tab:last-child {
    margin-top: 10px;
  }
  .react-tabs {
    padding: 0 10px;
  }
}

.carousel.slide {
  position: relative;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.carousel-inner {
  margin: 100px 0;
  overflow: hidden;
  position: relative;
  background: #fff;
}
.carousel-item.active {
  overflow: hidden;
  position: relative;
  background-color: #fff;
}
.leftText {
  float: left;
  background-color: #fff;
}

.leftText .location-svg {
  margin-left: 10px;
  margin-right: 20px;
}

.carousel-control-next .sr-only {
  display: none;
}
.carousel-control-prev .sr-only {
  display: none;
}

