@media screen and (min-width: 900px) and (max-width: 1440px) {
  a.carousel-control-prev .carousel-control-prev-icon {
    position: absolute;
    top: 670px;
    left: 1px;
  }
  a.carousel-control-next .carousel-control-next-icon {
    position: absolute;
    top: 670px;
    right: 1px;
  }
}

.carousel .carousel-caption {
  position: unset;
}

.carousel-control-prev {
  display: none;
}
.carousel-control-next {
  display: none;
}

@media (min-width: 1260px) {
  .leftText {
    padding-left: 50px;
    width: 50%;
  }
}

.select {
  font-family: Poppins;
  margin-top: 20px;
  height: 38px;
}

.slider {
  width: 70%;
  margin-top: 25px;
}

.react-datepicker-wrapper {
  margin-top: 20px;
}

.react-datepicker__input-container input {
  height: 38px;
  border-radius: 3px;
  font-family: Poppins;
  padding-left: 10px;
}

.material-icons {
  width: 70px;
  height: 70px;
  border-radius: 15px;
  background-image: linear-gradient(to right,#4cb8c4,#3cd3ad);
}
@media screen and (max-width: 768px) {
  .material-icons {
    width: 60px;
    height: 60px;
  }
}

.material-icons.md-dark {
  color: azure;
}

.material-icons.md-24 {
  font-size: 58px;
  padding: 6px;
}
@media screen and (max-width: 768px) {
  .material-icons.md-24  {
    font-size: 50px;
    padding: 5px;
  }
}
.mb-3 {
  font-family: Poppins;
  margin-top: 30px;
  margin-bottom: 1.5rem !important;
}

.nav-link {
  font-size: 25px;
  color: rgb(58, 178, 179);
}

.card-text {
  float: right;
  width: 77%;
  font-family: Poppins;
  line-height: normal;
}
@media screen and (max-width: 2560px) {
  .mb-3 {
    width: 90%;
    margin-left: 130px;
  }
}
@media screen and (max-width: 1440px) {
  .mb-3 {
    width: 90%;
    margin-left: 70px;
  }
}
@media screen and (max-width: 768px) {
  .mb-3 {
    width: 90%;
    margin-left: 40px;
  }
}
@media screen and (max-width: 525px) {
  .mb-3 {
    width: 90%;
    margin-left: 35px;
  }
}
@media screen and (max-width: 375px) {
  .mb-3 {
    width: 90%;
    margin-left: 25px;
  }
}
@media screen and (max-width: 320px) {
  .mb-3 {
    width: 90%;
    margin-left: 20px;
  }
}

.mapboxgl-ctrl-attrib-inner {
  display: none;
}

.marker {
  background-image: url(data:image/png;base64,);
  background-size: cover;
  width: 32px;
  height: 40px;
  cursor: pointer;
}

.loadEffect span:nth-child(8) {
  bottom: 20px;
  left: 10px;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
  -webkit-animation-delay: 1.04s;
          animation-delay: 1.04s;
}

